  try {
    const response = await updateRunningApi({userId: Number(userModuleDataGet.value.id), runingInfoDtoItems: originalDb})
    if (response) {
      console.log('')
    } else {
      console.error('   ');
    }
  } catch (error) {
    console.error('Error:', error);
  }
}


